/**/
.app {
    position: absolute;
    z-index: 10000;
    overflow: hidden;
    animation: perexod 2.1s;
    background: radial-gradient(closest-side, #3f87a6, #ebf8e1, #f69d3c);
}

@keyframes perexod {
    0% {

        left: 50%;
        top: 50%;
        border-radius: 100%;
        height: 0;
        width: 0;
    }
    100% {
        left: -30vw;
        top: -30vh;
        border-radius: 50%;
        height: 160vh;
        width: 160vw;
    }

}

.myImage {
    position: fixed;
    z-index: 5002;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.9);
}

.myImage img {
    margin: auto;
    display: block;
    max-height: 100%;
    max-width: 100%;
    animation: zoom 1s;
}

.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}

.close:hover {
    color: #bbb;
    cursor: pointer;
}

@keyframes zoom {
    from {
        transform: scale(0)
    }
    to {
        transform: scale(1)
    }
}


.root {
    background: url("data:image/jpeg;base64,");
    background-size: cover;
    background-attachment: fixed;
}

.top img {
    position: fixed;
    width: 30vw;
    top: -8vw;
    right: 15vw;
    z-index: 999;
}

section div {
    position: fixed;
    max-width: 3em;
    max-height: 3em;
    z-index: 1000;

}

section img {
    width: 100%;
    height: 100%;
}

.leaf1 {
    left: 90%;
    animation: anim 9s linear infinite;
}

.leaf2 {
    left: 80%;
    animation: anim 17s linear infinite;
}

.leaf3 {
    left: 70%;
    animation: anim 13s linear infinite;
}

.leaf4 {
    left: 59%;
    animation: anim 15s linear infinite;
}

.leaf5 {
    left: 48%;
    animation: anim 10s linear infinite;
}

.leaf6 {
    left: 37%;
    animation: anim 14s linear infinite;
}

.leaf7 {
    left: 26%;
    animation: anim 8s linear infinite;
}

.leaf8 {
    left: 15%;
    animation: anim 14s linear infinite;
}

.leaf3 img {
    max-height: 3em;
    max-width: 1.5em;
}

@keyframes anim {
    0% {
        top: -10%;
        transform: translateX(20px) rotate(0deg) rotateZ(0deg);
    }
    20% {
        transform: translateX(-20px) rotate(45deg);
    }
    40% {
        transform: translateX(20px) rotate(90deg);
    }
    60% {
        transform: translateX(20px) rotate(180deg);
    }
    80% {
        transform: translateX(-20px) rotate(200deg);
    }
    100% {
        top: 110%;
        transform: translateX(20px) rotate(240deg) rotateZ(90deg)
    }
}

@keyframes example {
    0% {
        transform: rotateZ(0deg);
        width: 100%;
        height: 100%;
    }
    100% {
        transform: rotateZ(180deg);
        width: 0%;
        height: 0;
    }
}

.App2 {
    transform-origin: 660px 400px;
    animation-name: example;
    animation-duration: 2s;
}

.reactLogoSmall {
    position: fixed;
    width: 20vw;
    bottom: -8vw;
    left: 40vw;
    cursor: pointer;
    transform-origin: 10vw 48%;
    z-index: 5001;
    animation: rlogo 3s linear infinite;
    transition: 1s;
}

.reactLogoSmall:hover {
    width: 24vw;
    bottom: -6vw;
    transform-origin: 12vw 48%;
    left: 38vw
}

.reactLogoLarge {

    position: fixed;
    width: 68vw;
    z-index: 1000;
    bottom: 3vw;
    left: 16vw;
    transition: 1s;

}

#image:hover {
    cursor: pointer;
    opacity: 0.7;
}

.App-logo {
    width: 100%;
}

.materalize, .pureCss, .uzb, .rus {
    position: absolute;
    transform-origin: center;
    font-weight: 500;
    z-index: 2000;
    transition: 1s;
    color: red;
    text-shadow: 0px 0px 5px yellow,
    0px 0px 7px lime,
    0px 0px 10px dodgerblue;
}

.materalize {
    transform: rotateZ(-30deg);
    left: 24vw;
    top: 13vw;
}

.pureCss {
    transform: rotateZ(30deg);
    right: 24vw;
    top: 13vw;
}

.uzb {
    transform: rotateZ(30deg);
    left: 26vw;
    top: 35vw;
}

.rus {
    transform: rotateZ(-30deg);
    right: 26vw;
    top: 35vw;
}

.materalize:hover, .pureCss:hover, .uzb:hover, .rus:hover {
    transform: rotateZ(0deg);
    background: rgba(1, 10, 29, 0.94);
    color: gray;
    cursor: pointer;
    font-size: 2em;
    top: 12vw;
    text-shadow: 0px 0px 5px white,
    0px 0px 10px white,
    0px 0px 15px #d7e4ed,
    0px 0px 20px #d7e4ed,
    0px 0px 25px #d7e4ed,
    0px 0px 30px #dddddd;

    letter-spacing: .2em;
    z-index: 2001;
}

.yes {
    transition: 1s;
    display: inline;
    position: fixed;
    width: 68vw;
    height: 50vw;
    z-index: 1001;
    bottom: 5vh;
    left: 16vw;

}

.not {
    display: none;
}

.uzb:hover {
    top: 34vw;
    left: 25vw;
    background: linear-gradient(dodgerblue, white, limegreen);
}

.rus:hover {
    top: 34vw;
    right: 25vw;
    background: linear-gradient(white, blue, red);
}

.dvig {
    position: absolute;
    cursor: pointer;
    top: 27vw;
    left: 36vw;
    width: 5vw;
    height: 5vw;
    z-index: 2500;
    animation: dug 2s ease-in-out infinite;
}

@keyframes dug {
    0% {
        width: 5vw;
        height: 5vw;
        top: 22.5vw;
        left: 31.5vw;
    }
    50% {
        width: 6vw;
        height: 6vw;
        top: 22vw;
        left: 31vw;
    }
    100% {
        width: 5vw;
        height: 5vw;
        top: 22.5vw;
        left: 31.5vw;
    }
}

.dvig img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 600px) {
    .materalize, .pureCss, .uzb, .rus, .dvig {
        display: block;
        position: sticky;
        margin: auto;
        text-align: center;
        transform: rotateZ(0);
        animation: none;
        left: auto;
        right: auto;
        padding-bottom: 10px;
    }

    .dvig {
        width: 40px;
        height: 40px;
    }

    .yes {
        background: rgba(221, 221, 221, .5);
    }

    .reactLogoSmall {
        width: 30vw;
        bottom: -12vw;
        left: 35vw;
        transform-origin: 15vw 46%;
        transition: 1s;
    }

    .reactLogoSmall:hover {
        width: 36vw;
        bottom: -6vw;
        transform-origin: 18vw 46%;
        left: 32vw
    }


}

@keyframes rlogo {

    0% {
        transform: rotateZ(60deg)
    }
    20% {
        transform: rotateZ(120deg)
    }
    40% {
        transform: rotateZ(180deg)
    }
    60% {
        transform: rotateZ(240deg)
    }
    80% {
        transform: rotateZ(300deg)
    }
    100% {
        transform: rotateZ(360deg)
    }
}
