
.shrift {
    font-size: 5pt;
}

.ltitle {
    font-family: "Monotype Corsiva";
    font-size: 1.5em;
}

.malumot {
    position: relative;
    width: auto;
    margin-top: 5em;
    padding-bottom: 2em;
    padding-left: 0;
}

.malumot:before {
    position: absolute;
    left: 2em;
    top: 0;
    content: '  ';
    display: block;
    width: 4px;
    height: 100%;
    margin-left: -3px;
    background: #d7e4ed;
    z-index: 5;
}

.korobka {
    position: relative;
    width: 80%;
    padding: 1.5em 1em;
    background: #fff;
    box-shadow: 0 3px 0 #d7e4ed;
    float: right;
}

.malumot p {
    letter-spacing: .5px;
    font-size: 1.1em;
    line-height: 1.5em;
    margin: .5em;
    margin-top: 1.5em;
    font-family: "Dosis";
}


.timeline-icon {
    position: absolute;
    background: white;
    top: -.7em;
    width: 2em;
    height: 2em;
    left: 2.2em;
    margin-left: -1.35em;
    box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0), 0 0 0 5px rgba(0, 0, 0, 0.15);
    border-radius: 50%;
    z-index: 10;
}

.teg {
    position: absolute;
    background: #d7e4ed;
    padding-left: 3em;
    top: -.7em;
    padding-top: .5em;
    width: 28em;
    height: 2.2em;
    left: 2em;
    z-index: 9;
}

.timeline-blocks {
    display: block;
    height: auto;
    position: relative;
    overflow: hidden;
}

.leftMaterial {
    height: 100%;
    position: relative;
    margin-top: 2em;
    transition: width 2s;

}

b {
    font-size: .5em;
}

.leftMaterial img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    max-height: 7rem;
    border: white solid;

}

.contact {
    margin-top: 15vh;
}

.title {
    font-weight: 800;
    font-family: Calibri;
}

.menu {
    position: absolute;
    display: none;
    top: 20px;
    right: 20px;
}

@media screen and (max-width: 600px) {
    .container {
        width: 100%;
        margin: 0;
    }

    .leftMaterial {
        display: none;
        transition: 1s;
    }

    .row .col.s8 {
        width: 100%;
    }

    .menu {
        display: inline;
        z-index: 4001;
    }

    .mobile {
        display: inline;
        animation: ann 1s;
        border: darkorange solid;
        overflow: hidden;
        position: absolute;
        left: 0;
        top: 0;
        padding: 15px;
        padding-top: 30px;
        background: #33435a;
        color: white;
        width: 100%;
        z-index: 4000;
        transition: 1s;

    }

    .mobile img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 50%;
        max-height: 10rem;
        border: white solid;
    }

    @keyframes ann {
        0% {
            top: -20%;
            left: 120%;;
            width: 0;
            border-radius: 50%
        }
        100% {
            top: 0;
            right: 0;
            width: 100%;
            border-radius: 0%
        }
    }
}