.resumeContainer {
    margin: 0 auto;
    width: 70%;
    min-height: 100vh;
    background-color: #e3d4c1;
}

.resumeImage {
    position: absolute;
    transform-origin: center;
    transform: rotateZ(45deg);
    width: 48vw;
    height: 48vw;
    left: 26vw;
    overflow: hidden;
    top: -30vw;
    background: #33435a;
    transition: 2s;

}

.Text {
    position: absolute;
    bottom: 11vw;
    right: 2vw;
    width: 28vw;
    text-align: center;
    transform-origin: center;
    transform: rotateZ(-45deg);
    font-family: "Monotype Corsiva";

}

.Name {
    display: block;
    color: darkorange;
    font-size: 4vw;
}

.Proffession {
    display: block;
    color: #d7e4ed;
    font-size: 1.5vw;
}

.photo {
    position: absolute;
    background: white;
    bottom: 2vw;
    right: 2vw;
    width: 8vw;
    height: 8vw;
    overflow: hidden;
    border-bottom-left-radius: 20%;
    border-top-right-radius: 20%;
}

.photo img {
    width: 130%;
    height: 130%;
    transform: rotateZ(-45deg);
}

#info {
    display: none;
}

.resumeHead {
    background: darkorange;
    width: 100%;
    height: 7vw;
}

.resumeAbout {
    overflow: hidden;
    width: 100%;
}

.AboutMe {
    display: block;
    min-height: 22vw;
    font-size: 1.3vw;
    padding: 1vw;
}

.AboutAll {
    text-align: center;
}

.Hd {
    font-family: "Monotype Corsiva";
    font-size: 2.5vw;
}

.Right {
    text-align: right;
    float: right;

}

.Left {
    text-align: left;
    float: left;
}

.kontaktlar, .malumotlarim {
    display: block;
    font-size: 1vw;
    color: orange;
    text-align: left;
    margin-top: 1vw;
    font-family: "lucida sans unicode";
}

i img {
    width: 2vw;
    height: 2vw;

}

span a {
    margin-left: 1vw;
    text-decoration: none;
    color: darkorange;
}

.AboutAll .Hd {
    font-size: 4vw;
    border-bottom: solid #33435a;
    padding-left: 2vw;
    padding-right: 2vw;
}

.disp span {
    display: block;
}

.ikonka {
    width: 4vw;
    height: 4vw;
    position: relative;
    top: 0.7vw;
}

.Year {
    margin-top: 2vw;
    font-weight: 600;
    font-size: 2vw;
}

.School {
    font-size: 1.5vw;
    color: darkorange;
}

.edu {
    margin-bottom: 3vw;
}

.myProgress {
    width: 35vw;
    margin: auto;
    border-radius: 3vw;
    background-color: #ddd;
    overflow: hidden;
    margin-bottom: 1.5vw;
    height: 2.5vw;
}

.myBar {
    height: 2.5vw;
    background: linear-gradient(45deg, darkorange, chartreuse, pink);

}

.tech {
    position: relative;
    top: -2.5vw
}

.tech img {
    margin-right: 1vw;
    position: relative;
    width: 2vw;
    height: 2vw;
    bottom: .5vw;
}

#footer {
    margin-top: 5vw;
    width: 100%;
    background: linear-gradient(90deg, darkorange, yellow, darkorange);
    height: 4vw;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 600px) {
    .resumeContainer {
        width: 100%;
    }

    .resumeImage {
        width: 70vw;
        height: 70vw;
        left: 15vw;
        top: -67vw;
    }

    .Text {
        display: none;
    }

    #info {
        display: block;
        width: 100%;
        height: 100%;
        transform: rotateZ(-45deg);
    }

    .resumeHead {
        height: 10vh;
    }

    .AboutMe {
        font-size: 1em;
        padding: 1em;
        margin: auto;
    }

    .Hd {
        font-size: 2em;
    }

    .Right {
        margin-top: 1.5em;
        text-align: center;
        float: none;
    }

    .Left {
        margin-top: 1.5em;
        text-align: center;
        float: none;
    }

    .kontaktlar, .malumotlarim {
        font-size: 1em;
        text-align: center;
        margin-top: 1em;
    }

    i img {
        width: 1em;
        height: 1em;

    }

    span a {
        margin-left: 15px;
    }

    .AboutAll .Hd {
        font-size: 2em;
        padding-left: 1.5em;
        padding-right: 1.5em;
    }

    .ikonka {
        width: 1em;
        height: 1em;

    }

    .Year {
        margin-top: 1em;
        font-weight: 600;
        font-size: 1em;
    }

    .School {
        font-size: 1.5em;
    }

    .edu {
        margin-bottom: 2em;
    }

    .myProgress {
        width: 80%;
        border-radius: 1em;
        margin-bottom: .8em;
        height: 1.5em;
    }

    .myBar {
        height: 1.5em;
    }

    .tech {
        top: -1.6em;
    }

    .tech img {
        margin-right: .3em;
        width: 1em;
        height: 1em;
        bottom: .2em;
    }

    #footer {
        margin-top: 5vw;
        width: 100%;
        background: linear-gradient(90deg, darkorange, yellow, darkorange);
        height: 4vw;
        margin-left: auto;
        margin-right: auto;
    }
}